import svg4everybody from 'svg4everybody';
import $ from 'jquery';
import 'slick-carousel';
import '../../node_modules/mixitup'

$(() => {
	svg4everybody();
	$('.reviews__list').slick({
		dots: true,
		infinite: true,
		speed: 300,
		slidesToShow: 1,
		arrows: false,
		customPaging: function () {
			return '<a class="reviews__dot"></a>';
		}
	});
});



